import React from "react";
import { atlasAPI } from "../endpoints";

export const KeyManager = {

    createKey: async (userToken, orgId, eventId, key) => {
        const config = {
          headers: {
            "Authorization": userToken,
          },
        };
        
        try {
          return await atlasAPI.post(
            '/keys/' + orgId + '/' + eventId,
            key,
            config
          )
        } catch (error) {
          console.log(error);
          return null;
        }
      },

}